import React, { Component } from "react";
import { connect } from "react-redux";
import { reduxForm } from "redux-form";
import {
  getClosePengambilanTimbangan,
  getClosePengambilanTimbanganSelected,
} from "../actions/closeTimbangan";
import { showModal } from "../actions/ModalAction";
import ModalMasterData from "../DataMaster/Component/ModalMasterData";
import GlobalTabel from "../GlobalComponent/GlobalTabel";
import HeaderCard from "../HeaderCard";
import { formatGram, formatRp } from "../helper";
import FormBatalPengambilanTimbang from "./Component/FormBatalPengambilanTimbang";

const maptostate = (state) => {
  return {
    listCloseTimbanganSelected: state.provinsi.listCloseTimbanganSelected,
    listCloseBarcode: state.provinsi.listCloseTimbangan,
  };
};

class BatalPengambilanTimbang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      items: [
        {
          value: "TERIMA SUPPLIER",
          name: "TERIMA SUPPLIER",
        },
        {
          value: "PENJUALAN",
          name: "PENJUALAN",
        },
        {
          value: "PINDAH BARANG",
          name: "PINDAH BARANG",
        },
        {
          value: "RETURN CUSTOMER",
          name: "RETURN CUSTOMER",
        },
      ],
      columns: [
        {
          dataField: "tanggal",
          text: "Tanggal",
        },
        {
          dataField: "baki",
          text: "Nama\nBaki",
        },
        {
          dataField: "kode_customer",
          text: "Lokasi",
        },
        {
          dataField: "jenis_trx",
          text: "Jenis\nTrx",
        },
        {
          dataField: "total_bruto",
          text: "Berat\n(Gr)",
          align: "right",
          headerAlign: "right",
          formatter: (data) => formatGram(data),
        },
        {
          dataField: "total_netto",
          text: "Netto\n(Gr)",
          align: "right",
          headerAlign: "right",
          formatter: (data) => formatGram(data, 3),
        },
        {
          dataField: "total_gross",
          text: "Gross\n(Gr)",
          align: "right",
          headerAlign: "right",
          formatter: (data) => formatGram(data),
        },
        {
          dataField: "total_ongkos",
          text: "Ongkos\n(Rp)",
          align: "right",
          headerAlign: "right",
          formatter: (data) => formatRp(data),
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-warning mr-2"
                  onClick={() =>
                    this.showDetail(row.detail_barang, row.no_timbang)
                  }
                >
                  Detail
                </button>
              </div>
            );
          },
        },
      ],

      data: [
        {
          tanggal: "2020",
        },
      ],
    };
  }

  showDetail(data, no_timbang) {
    this.props.dispatch(showModal());
    this.props.dispatch(getClosePengambilanTimbanganSelected(data, no_timbang));
  }
  componentDidMount() {
    this.props.dispatch(getClosePengambilanTimbangan());
  }

  handleSubmit(data) {
    console.log(data);
  }
  selected(data) {
    console.log(data);
  }
  render() {
    return (
      <div className="container-fluid">
        <div className="row">
          <div className="col-lg-12">
            <div className="card">
              <HeaderCard title="Batal Pengambilan Timbang Barang" />
              <div className="card-body">
                <div className="col-lg-12 col-md-12">
                  <GlobalTabel
                    keyField="kodeJenis"
                    data={this.props.listCloseBarcode || []}
                    columns={this.state.columns}
                    textEmpty="Silahkan Tambah Data Dengan Klik Tombol Biru Diatas"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
        <ModalMasterData
          content={<FormBatalPengambilanTimbang />}
          title="Detail Barang"
        />
      </div>
    );
  }
}

BatalPengambilanTimbang = reduxForm({
  form: "BatalPengambilanTimbang",
  enableReinitialize: true,
})(BatalPengambilanTimbang);
export default connect(maptostate, null)(BatalPengambilanTimbang);
